// loader
const body = document.body
window.addEventListener('load', ()=>{
   body.classList.add('loaded')
})
// burger
function burgerMenu() {
   const burger = document.querySelector('.burger')
   const menu = document.querySelector('.nav__menu')
   const body = document.querySelector('body')
   burger.addEventListener('click', () => {
      if (!menu.classList.contains('active')) {
         menu.classList.add('active')
         burger.classList.add('active')
         body.classList.add('locked')
      } else {
         menu.classList.remove('active')
         burger.classList.remove('active')
         body.classList.remove('locked')
      }
   })
}
burgerMenu();
// fixed nav
function fixedNav() {
   const nav = document.querySelector('nav')
   const breakpoint = 1
   if (window.scrollY > breakpoint) {
      nav.classList.add('fixed__nav')
   } else {
      nav.classList.remove('fixed__nav')
   }
}
window.addEventListener('scroll', fixedNav)
//
document.addEventListener('DOMContentLoaded', function () {
   let lazyImages = [].slice.call(document.querySelectorAll('img[loading=lazy]'));
   let active = false;
   const lazyLoad = function () {
      if (active === false) {
         active = true;
         setTimeout(function () {
            lazyImages.forEach(function (lazyImage) {
               if ((lazyImage.getBoundingClientRect().top <= window.innerHeight && lazyImage.getBoundingClientRect().bottom >= 0) && getComputedStyle(lazyImage).opacity !== '0') {
                  lazyImage.src = lazyImage.dataset.src;
                  lazyImage.srcset = lazyImage.dataset.srcset;
                  lazyImages = lazyImages.filter(function (image) {
                     return image !== lazyImage;
                  });
                  if (lazyImages.length === 0) {
                     document.removeEventListener('scroll', lazyLoad);
                     window.removeEventListener('resize', lazyLoad);
                     window.removeEventListener('orientationchange', lazyLoad);
                  }
               }
            });
            active = false;
         }, 500);
      }
   };
   document.addEventListener('scroll', lazyLoad);
   window.addEventListener('resize', lazyLoad);
   window.addEventListener('orientationchange', lazyLoad);
});
//
if (document.querySelector('.work__swiper')) {
   new Swiper('.swiper-container', {
      direction: 'horizontal',
      loop: !0,
      speed: 1000,
      slidesPerView: 1,
      spaceBetween: 30,
      //effect: "fade",
      keyboard: {
         enabled: !0,
         onlyInViewport: !1
      },
      navigation: {
         nextEl: '.swiper-next',
         prevEl: '.swiper-prev',
      },
      pagination: {
         el: '.swiper-pagination',
         clickable: true,
      },
   });
}
// 
let forms = document.querySelectorAll('.formvalid')
for (let i = 0; i < forms.length; i++) {
   const submit = forms[i].querySelector('button');
   submit.addEventListener('click', validate);
   function validate(e) {
      e.preventDefault();
      let inputs = forms[i].querySelectorAll('input');
      let textareas = forms[i].querySelectorAll('textarea');
      let valid = true;
      for (let a = 0; a < inputs.length; a++) {
         let input = inputs[a]
         if (!input.value) {
            input.classList.add('invalid');
         } else {
            input.classList.remove('invalid');
         }
      }
      for (let a = 0; a < textareas.length; a++) {
         let textarea = textareas[a]
         if (!textarea.value) {
            textarea.classList.add('invalid');
         } else {
            textarea.classList.remove('invalid');
         }
      }
      return valid;
   }
}